import React, {Component} from 'react'

class App extends Component {
  state = {
    data: [],
  }

  // Code is invoked after the component is mounted/inserted into the DOM tree.
  componentDidMount() {
    const url =
      'https://upc.shamacon.us/grocy/5449000009500'

    fetch(url)
      .then((result) => result.json())
      .then((result) => {
        this.setState({
          data: result
        })
      })
  }

  render() {
    const {data} = this.state
    console.log("here is the data: ")
    console.log(data)
    const obj = Object.entries(data).map(([key, value]) => ({key, value}))
    console.log("here is the object")
    console.log(obj)
    const result = obj.map((entry, index) => {
        console.log("here is the entry: ");
        console.log(entry);
        return <li key={index}>{entry.key}: {entry.value}</li>;
    });

    return <div className="container"><ul>{result}</ul></div>;
}
}

export default App;